import React, { PropTypes } from 'react';
import { Link } from 'react-router';
import SlotSelector from './SlotSelector';
import DaySelector from './DaySelector';
import { Wrapper, Block, Logo, Header2, Title2, Button3, Dates, Center, Title3 }
  from './styledComponents';
import ProgressBar from './order/ProgressBar';
import LogoImg from '../assets/logo1.png';
import { trackEvent } from '../services/analytics';

const Step4 = ({ changeRDVDay, changeRDVHour, isValid, goNext }) => (
  <Wrapper>
    <Block>
      <Header2>
        <Link
          to={'/'} style={{ flex: 1, textAlign: 'center' }}
          onClick={() => trackEvent('click', 'home_step4')}
        >
          <Logo src={LogoImg} />
        </Link>
        <ProgressBar step={3} />
        <Title2>
          Mon Rendez-vous
        </Title2>
      </Header2>
      <Dates>
        <Title3>Jour</Title3>
        <DaySelector onChange={changeRDVDay} />
        <Title3>Heure</Title3>
        <SlotSelector onChange={changeRDVHour} />
      </Dates>
      <Center>
        <Button3 isNotValid={!isValid} onClick={goNext}>{'> suivant <'}</Button3>
      </Center>
    </Block>
  </Wrapper>
);

Step4.propTypes = {
  changeRDVHour: PropTypes.func.isRequired,
  changeRDVDay: PropTypes.func.isRequired,
  isValid: PropTypes.bool,
  goNext: PropTypes.func.isRequired,
};

Step4.defaultProps = {
  isValid: false,
};

export default Step4;



