import React, { PropTypes } from 'react';
import { Slots, Slot } from './styledComponents';

function createSlot(hours, is30 = false) {
  return {
    hours,
    minutes: is30 ? 30 : 0,
    string: is30 ? `${hours}h30 - ${hours + 1}h` : `${hours}h - ${hours}h30`,
  };
}

const slots = [];
[17, 18, 19, 20, 21].forEach((slotHour) => {
  slots.push(createSlot(slotHour));
  slots.push(createSlot(slotHour, true));
});
slots.pop(); // remove the forbidden 21h30 - 22H slot

class SlotSelector extends React.Component {
  constructor() {
    super();
    this.state = {};
  }

  selectSlot(slot) {
    this.setState({ slot });
    this.props.onChange(slot.hours, slot.minutes);
  }

  render() {
    return (
      <Slots>
        {slots.map(slot =>
          <Slot
            isSelected={this.state.slot === slot}
            key={slot.string}
            onClick={() => this.selectSlot(slot)}
          >{slot.string}</Slot>)}
      </Slots>
    );
  }
}

SlotSelector.propTypes = {
  onChange: PropTypes.func.isRequired,
};

export default SlotSelector;



