import React, { PropTypes } from 'react';
import { connect } from 'react-redux';
import { browserHistory } from 'react-router';
import Step4 from '../../components/Step4';
import { trackEvent } from '../../services/analytics';

class Step4Container extends React.Component {
  constructor() {
    super();
    this.state = {};
    this.changeRDVDay = this.changeRDVDay.bind(this);
    this.changeRDVHour = this.changeRDVHour.bind(this);
    this.goNext = this.goNext.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    if (window.location.hostname !== 'localhost') {
      window.onbeforeunload = function () {
        return true;
      };
    }
  }

  changeRDVHour(hours, minutes) {
    trackEvent('choix-heure-rdv', `${hours}h${minutes}`);
    this.setState({ isHourSet: true });
    this.props.dispatch({ type: 'ORDER.SET_RDV_HOUR', hours, minutes });
  }

  changeRDVDay(day) {
    trackEvent('choix-jour-rdv', day.format('L'));
    this.setState({ isDaySet: true });
    this.props.dispatch({ type: 'ORDER.SET_RDV_DAY', day });
  }

  goNext() {
    if (this.isValid()) {
      browserHistory.push('/order/step5');
    } else {
      if (!this.state.isDaySet && !this.state.isHourSet) {
        trackEvent('erreur', 'pas-choix-jour-heure');
      } else if (!this.state.isDaySet) {
        trackEvent('erreur', 'pas-choix-jour');
      } else {
        trackEvent('erreur', 'pas-choix-heure');
      }
      this.setState({ hasError: true });
    }
  }

  isValid() {
    return this.state.isDaySet && this.state.isHourSet;
  }

  render() {
    return (
      <Step4
        changeRDVDay={this.changeRDVDay}
        changeRDVHour={this.changeRDVHour}
        isValid={this.isValid()}
        goNext={this.goNext}
      />
    );
  }
}

Step4Container.propTypes = {
  dispatch: PropTypes.func.isRequired,
};

const mapStateToProps = () => ({});
const mapDispatchToProps = dispatch => ({ dispatch });
export default connect(mapStateToProps, mapDispatchToProps)(Step4Container);



